import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

EditNewtork.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function EditNewtork({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const NewUserSchema = Yup.object().shape({
    plan_id: Yup.string().required('')
  });
  const defaultValues = useMemo(
    () => ({
      network: currentUser?.network,
      Habukhan_id: currentUser?.Habukhan_id,
      msorg_id: currentUser?.msorg_id,
      virus_id: currentUser?.virus_id,
      boltnet_id: currentUser?.boltnet_id,
      autopilot_id: currentUser?.autopilot_id,
      plan_id: currentUser?.plan_id
    }),
    [currentUser]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    defaultValues,
  });

  const {
    reset,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/network/plan/new/habukhan/safe/url/${AccessToken}/secure`, data);
      enqueueSnackbar('Update success!');
      navigate(PATH_ADMIN.plan.network);
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  }



  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="network" label="Network" disabled />
              <RHFTextField name="Habukhan_id" label="Habukhan ID" />
              <RHFTextField name="msorg_id" label="MSORG ID" />
              <RHFTextField name="virus_id" label="VIRUS ID" />
              <RHFTextField name="boltnet_id" label="BoltNet ID" />
              <RHFTextField name="autopilot_id" label="Autopilot ID" />
            </Box>

            <Stack alignItems="flex-end" sx={{
              mt: 2, display: 'grid',
              columnGap: 1,
              rowGap: 2, gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
            }}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                save changes
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
