import { useState, useEffect, } from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
// @mui
import { Container,Backdrop,CircularProgress } from '@mui/material';

// routes
import { PATH_ADMIN} from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import EditNewtork from '../../sections/admin/editnetwork';
import axios from '../../utils/axios';



export default function NetworkEdit(){
    const { themeStretch } = useSettings();
    const { pathname } = useLocation();
    const { name = '' } = useParams();
    const isEdit = pathname.includes('edit');
    const navigate = useNavigate();
    const [currentUser, SetCurrentUser] = useState();
    const [load,SetLoad] = useState(true)
    const AccessToken =  window.localStorage.getItem('accessToken');
    useEffect(()  => {
        if(name){
        axios.post(`/api/system/network/plan/edit/user/safe/url/${AccessToken}/secure`,{
            plan_id: name
        }).then(data => {
               SetCurrentUser(data.data?.plan); 
               SetLoad(false)
               }).catch(() =>  {navigate(PATH_ADMIN.plan.network); SetLoad(false)});
            }else{
                navigate(PATH_ADMIN.plan.network);
                SetLoad(false)
            }
       
        }, [AccessToken, name, SetCurrentUser, navigate]);  
    return (
      <Page title={"Plan: " && !isEdit ? 'Add Disco Plan' : 'Edit Disco Plan'}>
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading={`Edit Network Plan =>  ${!currentUser?.network ? '' : currentUser?.network}`}
            links={[
              { name: 'Dashboard', href: PATH_ADMIN.root },
              { name: 'Network Plans', href: PATH_ADMIN.plan.network },
              { name: !isEdit ? 'Network' : currentUser?.network },
            ]}
          />
  
          <EditNewtork isEdit={isEdit} currentUser={currentUser} />
          <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
        </Container>
      </Page>
    );
}
